import React from 'react'
import { Anchor } from 'antd'

const { Link } = Anchor

export const Navbar = (props) => {

  const state = props.state
  const listOfAnchorsLinks = state.map((el, index) => {
    return (
      <Link
        href={`#serial_${el.id}`}
        title={el.name}
        key={index}
      />
    )
  })

  return (
    <div className='navbar'>
      <h3 className='navbar__title'>Chose your serial:</h3>
      <Anchor offsetTop={0}>
        {listOfAnchorsLinks}
      </Anchor>
    </div>
  )
}